body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.wrapper .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.wrapper .items .item {
  box-sizing: border-box;
  margin-left: 15px;
  margin-right: 15px;
  word-wrap: break-word;
}

button {
  cursor: pointer;
}

header {
  position: relative;
  color: #ffffff;
}

header .container {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 10px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

header .container a {
  color: #ffffff;
  text-decoration: none;
}

header .container .items {
  margin: 20px 20px;
}

header .container .items:last-child {
  flex-grow: 1;
}

header .container .items:last-child .item {
  margin: auto;
}

header .container .items:last-child .item h1 {
  font-size: 3.5rem;
}

header .container .items:last-child .item p {
  font-size: 2.3rem;
}

header h1,
header p {
  text-align: center;
}

header .nav {
  display: flex;
  justify-content: space-between;
}

header nav {
  display: flex;
  justify-content: space-around;
}

header nav ul li {
  float: left;
  margin-right: 10px;
}

.menu {
  background-color: #f5f6f7;
  min-height: 65px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 25px;
}

.menu label {
  margin-right: 15px;
  padding-left: 5px;
}

.menu label,
.menu button {
  text-transform: uppercase;
}

.menu button {
  color: #ffffff;
  background-color: #aa9b77;
  padding: 10px 15px;
  border-radius: 15px;
}

.flex {
  display: flex;
}

.flex + .items {
  margin: 0;
}

.flex + .items > .item {
  width: calc(33.333333333333336% - 30px);
}

.flex + .items > .item .items:first-child,
.flex + .items > .item .items:last-child {
  justify-content: space-between;
}

.flex + .items > .item .items:nth-child(2) {
  justify-content: center;
}

footer .items:first-child {
  background-color: #aa9b77;
  padding: 30px;
}

footer .items:first-child h2 {
  font-size: 2rem;
}

footer .items:first-child p {
  font-size: 1.2rem;
  border: 2px solid #716854;
  padding: 30px;
}

footer .items:first-child h2,
footer .items:first-child p {
  text-align: center;
  color: #716854;
}

footer .items:nth-child(2) {
  background-color: #535253;
  color: #ffffff;
}

footer .items:last-child .item {
  width: calc(50% - 30px);
}

footer .items:last-child .item:last-child {
  text-align: right;
}

footer .items:last-child .item:last-child li {
  float: right;
  margin-right: 10px;
}

footer .items:last-child .item:last-child li::before {
  content: '|';
  margin-right: 10px;
}

footer .items:last-child .item:last-child p {
  clear: both;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}